import React from 'react';
import NavigationStyles from './CSS-styles/NavigationStyles.module.css';
import { Link } from 'react-router-dom'
import { logout } from './service/auth-service';
import { AuthContext } from './components/AuthContext';

function NavbarDesktop() {

    const loginContext = React.useContext(AuthContext);

    return (
        <nav className={NavigationStyles.Navigation}>

            <header className={NavigationStyles.topBar}>
                <span className='logo'>RnnR</span>
                <span>
                    {!loginContext.userData &&
                        <span>
                            <Link to="/">Home</Link>
                            <Link to="Bejelentkezes">Bejelentkezs</Link>
                            <Link to="Regisztracio">Regisztrci</Link>
                            {/* <Link to="FelhasznaloRegisztracio">Regisztrci felhasznlknt</Link>
                            <Link to="FutarRegisztracio">Regisztrls futrknt</Link> */}
                            <Link to="CsomagKovetes">Csomag nyomonkvets</Link>
                        </span>}


                    {loginContext.userData && loginContext.userData.role == "runner" &&
                        <span>
                            <Link to="FutarDashboard">Futr dashboard</Link>
                            <Link to="FutarraVaroCsomagok">Elrhet csomagok</Link>
                            <Link to="FolyamatbanLevoCsomagok">Szllts alatt</Link>
                            <Link to="FutarTerkep">Trkp</Link>
                            <Link to={`FutarProfil/${loginContext.userData.userName}`}>Sajt profil</Link>
                        </span>
                    }

                    {loginContext.userData && loginContext.userData.role == "user" &&
                        <span>
                            <Link to="FelhasznaloDashboard">Felhasznl dashboard</Link>
                            <Link to="CsomagFeladas">Csomag felads</Link>
                            <Link to="FeladottCsomagok">Feladott csomagok</Link>
                            <Link to="NekemCimzettCsomagok">Nekem cmzett csomagok</Link>
                            <Link to={`FelhasznaloProfil/${loginContext.userData.userName}`}>Sajt profil</Link>
                            <Link to="CsomagKovetes">Csomag nyomonkvets</Link>
                        </span>
                    }

                    {loginContext.userData && loginContext.userData.role == "superadmin" &&
                        <span>
                            <Link to="SaFutarReg">Futr reg</Link>
                            <Link to="SaFutarListazas">Futrlista</Link>
                            <Link to="SaCsomagLetrehozas">Csomagfelads</Link>
                            <Link to="SaCsomagListazas">Csomaglista</Link>
                            <Link to="SaCsomagFutarhozRendeles">Csomag hozzrendels</Link>
                            <Link to="SaTerkep">Trkp</Link>
                        </span>
                    }
                    {loginContext.userData &&
                        <span className={NavigationStyles.ExitButton}>
                            <Link to="/"
                                onClick={() => {
                                    logout().then(success => {
                                        loginContext.setUserData(null);
                                    })
                                }}>Kijelentkezs</Link>
                        </span>

                    }
                </span>
            </header>
        </nav>
    )
}

export default NavbarDesktop