import React from 'react'
import { AuthContext } from './components/AuthContext'
import HomeStyles from './CSS-styles/HomeStyles.module.css'
import { Link, useNavigate } from "react-router-dom";
import Card from './components/complex-components/Card';
import Cards from './components/complex-components/Cards';


function Home() {
  const loginContext = React.useContext(AuthContext);
  const [activeCard, setActiveCard] = React.useState(1)
  const navigate = useNavigate();

  return (
    <div className={HomeStyles.Container}>
      <div className={HomeStyles.HeroImg}>

          <p className={HomeStyles.MainSlogen}>
            {/* <span className={HomeStyles.RnnR}>RnnR </span><br></br> */}
            A kzssgi csomagkld alkalmazs</p>
          <p className={HomeStyles.SecondSlogen}>ahol brki lehet futr vagy felad</p>
        <button className={HomeStyles.StartButton} onClick={() => {
          navigate("/Regisztracio")
        }}>Regisztrci</button>
      </div>
      <div className={HomeStyles.Content}>
      <h2>Rlunk </h2>

      <div>
        <div className={HomeStyles.ParagraphDivs}>
          <p>Magyarorszgon elsknt indtottuk el a csomagkldsi szolgltatsunkat
            ahol brki lehet futr, akr tmegkzlekeds hasznlatval is.
            Csomagklds magnszemlyknt vagy cgknt egyszeren, hatkonyan, gyorsan, akr pr
            perces kiszlltsi idvel vroson bell.
          </p>
        </div>
        <div className={HomeStyles.BackgroundImg}></div>
        <div className={HomeStyles.ParagraphDivs}>
          <p>
            Rugalmas de biztonsgos csomagtvtel akr QR-kdos alapon.
            Kltsghatkony szolgltats teljes orszgos lefedettsggel.
            Kvesd nyomon a csomagjaid akr regisztrci nlkl is.
            Kivl lehetsg szmodra hogy extra jvedelemre tegyl szert, de ha szeretsz biciklizni/vezetni
            akkor itt akr a teljes lls lommunkdat is megtallhatod
          </p>
          <div className={HomeStyles.ButtonDiv}>
          <button className={HomeStyles.Start2Button} onClick={() => {
            navigate("/Regisztracio")
          }}>Vgj bele</button>
          </div>
          <Link to="/Bejelentkezes">Bejelentkezs</Link>
        </div>
      </div>
      
      <h2>Hasznos infk</h2>
      <div className={HomeStyles.ParagraphDivs}>
        <ul>
          <li><p>Lehetsz futr s csomagkld is.</p></li>
          <li><p>Megbzknt Kltsghatkony megoldst nyujtunk csomagjaid clbajuttatsra</p></li>
          <li><p>Megbzsokkal tmogatod futrainkat akik mindent megtesznek hogy megfelel idben s minsgben kiszlltsk a feladott csomagokat.</p></li>
          <li><p>Folyamatos gyflszolglattal rendelkeznk, amennyiben brmilyen problma felmerlne, azonnal itt lesznk hogy segtsnk.</p></li>

        </ul>

      </div>

      <h2>Felhasznli vlemnyek</h2>
      
      <Cards
        activeCard={activeCard}
        setActiveCard={setActiveCard}
        className={HomeStyles.SliderContainer}
      />
      {/* <h2>Kapcsolat</h2>
      
        <div className={HomeStyles.BackgroundImg2}>
          <p>E-mail: support@rnnr.hu</p>
          <p>Telefon:  +3611111111</p>
          <p>Szkhz: Budapest, random cm 1</p>
        </div>
       */}
    </div>
    </div>

  )
}

export default Home